.side_drawer_backbutton{
    position: fixed;
    float: right;
    margin-left: 17rem;
    margin-top: -3.5rem;
    background: maroon;
    border: none;
    outline: none;
    font-size: 2rem;
    color:white;
    z-index: 300;
    transform: translateX(-400%);
    transition: transform 0.3s ease-in;
    cursor: pointer;
}

.side_drawer_backbutton.open{
    transform: translateX(0);
}

.side_drawer_backbutton:hover,
.side_drawer_backbutton:active{
    color: aqua;
}