.side_drawer{
    height: 100%;
    background-image: linear-gradient(maroon, teal, rosybrown);
    box-shadow: 1px 0 7px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 50%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in;
}

.side_drawer.open{
    transform: translateX(0);
}

.side_drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side_drawer li{
    margin: 3rem 0;
}

.side_drawer a{
    color: white;
    text-decoration: none;
    font-size: 2.5rem;
}

.side_drawer a:hover,
.side_drawer a:active{
    color: aqua;
}
