.sidebar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: maroon;
    height: 56px;

}

.sidebar_navigation{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.sidebar_navigation-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.sidebar_navigation-items li{
    padding: 10px 2rem;
    flex: 10;
    font-size: 18px;
}

.sidebar_logo{
    margin-left: 1rem;
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
}

.sidebar_logo a{
    color: white;
    text-decoration: none;
}

.spacer{
    flex: 1;
}

.sidebar_navigation-items a{
    color:white;
    margin: 0 3rem 0 -5rem;
    font-size: 23px;
}

.sidebar_navigation-items a:hover{
    color: aqua;
}

@media (max-width: 768px) {
    .sidebar_navigation-items{
        display: none;
    }
}
